@import url('https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap');
body {
    margin: 0;
    font-family: 'Josefin Sans', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: white !important;
    

}
.container-fluid{
    width: 86% !important;
}
h2{
    font-size: 34px;
    font-weight: 700;
}  
h1{
    font-size: 65px !important;
    font-weight: 700 !important;
    position: relative;
    z-index: 1;
    line-height: 63px;
    padding: 8px 0;
    margin: 0;
    color: white !important;
}
.Header a:hover{
     color: #ff4800;
     transition: ease .5s;
}
.navbar{
    background:black;
}
.navbar-nav{
    float: right;
    text-transform: uppercase;
    flex-direction: row !important;
    font-weight: bold;
    z-index: 200;
}
.navbar-nav a{
    color: white;
}
.navbar-nav li{
  padding: 0px 0px 0px 60px;
}
.navbar-nav a:hover{
    text-decoration: none;
}
.navbar-brand{
    font-size: 58px !important;
    font-weight: bold;
    color: white;
	font-family:'Titan One',cursive;
}

/*nav underbar style */
.first::before {
  content: "";
  position: absolute;
  top: 70px;
  width: 50px;
  transform: scale(0);
  transform-origin: left;
  height: 3%;
  z-index: -1;
  background: #ff4800;
  -webkit-transition: 0.5s ease;
}
  .first:hover::before {
    content: "";
    position: absolute;
    top: 80px;
    transform: scale(1);
    height: 3%;
    z-index: -1;
    background: #ff4800;
    -webkit-transition: 0.5s ease;
  }
  
  .second::before {
    content: "";
    position: absolute;
    top: 70px;
    width: 55px;
    transform: scale(0);
    transform-origin: left;
    height: 3%;
    z-index: -1;
    background: #ff4800;
    -webkit-transition: 0.5s ease;
  }
    .second:hover::before {
      content: "";
      position: absolute;
      top: 80px;
      transform: scale(1);
      height: 3%;
      z-index: -1;
      background: #ff4800;
      -webkit-transition: 0.5s ease;
    }

    .third::before {
      content: "";
      position: absolute;
      top: 70px;
      width: 45px;
      transform: scale(0);
      transform-origin: left;
      height: 3%;
      z-index: -1;
      background: #ff4800;
      -webkit-transition: 0.5s ease;
    }
      .third:hover::before {
        content: "";
        position: absolute;
        top: 80px;
        transform: scale(1);
        height: 3%;
        z-index: -1;
        background: #ff4800;
        -webkit-transition: 0.5s ease;
      }
      .forth::before {
        content: "";
        position: absolute;
        top: 70px;
        width: 70px;
        transform: scale(0);
        transform-origin: left;
        height: 3%;
        z-index: -1;
        background: #ff4800;
        -webkit-transition: 0.5s ease;
      }
        .forth:hover::before {
          content: "";
          position: absolute;
          top: 80px;
          transform: scale(1);
          height: 3%;
          z-index: -1;
          background: #ff4800;
          -webkit-transition: 0.5s ease;
        }
        .fifth::before {
          content: "";
          position: absolute;
          top: 70px;
          width: 74px;
          transform: scale(0);
          transform-origin: left;
          height: 3%;
          z-index: -1;
          background: #ff4800;
          -webkit-transition: 0.5s ease;
        }
          .fifth:hover::before {
            content: "";
            position: absolute;
            top: 80px;
            transform: scale(1);
            height: 3%;
            z-index: -1;
            background: #ff4800;
            -webkit-transition: 0.5s ease;
          }

/*hero section style */
.herosection{
    background-color: black;
    
}
.herosection .row{
    height: 100vh !important;
}
.bgimage{
    background: url('./images/hero.png') no-repeat;
    height: 780px;
    background-repeat: no-repeat;
    background-size: 575px;
    background-position: 100%;
    position: relative;
    margin-top: -90px;

}
.herotext{
    padding-top: 3%;
}
.herotext p{
    font-size: 18px;
    font-weight: 200;
    margin-top: 30px;
}
.herotext h1::before{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 30px;
    height: 100%;
    z-index: -1;
    background: #ff4800;
}
.liquid-button {
    margin: auto;
    border-radius: 0px !important;
  }
  .liquid-button path {
    cursor: pointer;
  }
  
.btn{
    border: 2px solid #ff4800!important;
    background-color: #ff4800!important;
    border-radius: 0px !important;
    margin-right: 20px;
    margin-top: 30px;
}
.btn Link::before{
    content: "";
    position: absolute;
    height: 0;
    width: 51%;
    background: #ff4800;
    z-index: -1;
    -webkit-transition: .5s;
    transition: .5s;
}
.btn a{
    color: white !important;
}
.btn:hover{
    background: #000 !important;
}

/*mobile menue */ 
  
  @media screen and (max-width: 1050px) {
    .navbar-header{
        width: 100%;
        text-align: center;
    }
    .navbar-nav{
        text-align: center;
    }
  }
  @media screen and (max-width: 753px) {
    .navbar-nav a{
        padding: 0px 20px;
    }
  }
  @media screen and (max-width: 633px) {
    .navbar-nav a{
        padding: 0px 10px;
    }
  }
  @media screen and (max-width: 517px) {
    .navbar-nav a{
        padding: 0px 6px;
    }
  }
  @media screen and (max-width: 473px) {
    .navbar-nav a{
        padding: 16px 16px;
    }
  }

  /*mobile responsive home page */
  @media screen and (max-width: 768px) {
    .herosection {
        margin-top: -140px;
    }
  }