/*hero section style */
.herosection{
    background-color: black;
    
}
.herosection .row{
    height: 100vh !important;
}
.bgimage{
    background: url(/static/media/hero.a649c39e.png) no-repeat;
    height: 780px;
    background-repeat: no-repeat;
    background-size: 575px;
    background-position: 100%;
    position: relative;
    margin-top: -90px;

}
.herotext{
    padding-top: 3%;
}
.herotext p{
    font-size: 18px;
    font-weight: 200;
    margin-top: 30px;
}
.herotext h1::before{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 30px;
    height: 100%;
    z-index: -1;
    background: #ff4800;
}
.liquid-button {
    margin: auto;
    border-radius: 0px !important;
  }
  .liquid-button path {
    cursor: pointer;
  }
  
.btn{
    border: 2px solid #ff4800!important;
    background-color: #ff4800!important;
    border-radius: 0px !important;
    margin-right: 20px;
    margin-top: 30px;
}
.btn Link::before{
    content: "";
    position: absolute;
    height: 0;
    width: 51%;
    background: #ff4800;
    z-index: -1;
    -webkit-transition: .5s;
    transition: .5s;
}
.btn a{
    color: white !important;
}
.btn:hover{
    background: #000 !important;
}

/*mobile menue */ 
  
  @media screen and (max-width: 1050px) {
    .navbar-header{
        width: 100%;
        text-align: center;
    }
    .navbar-nav{
        text-align: center;
    }
  }
  @media screen and (max-width: 753px) {
    .navbar-nav a{
        padding: 0px 20px;
    }
  }
  @media screen and (max-width: 633px) {
    .navbar-nav a{
        padding: 0px 10px;
    }
  }
  @media screen and (max-width: 517px) {
    .navbar-nav a{
        padding: 0px 6px;
    }
  }
  @media screen and (max-width: 473px) {
    .navbar-nav a{
        padding: 16px 16px;
    }
  }

  /*mobile responsive home page */
  @media screen and (max-width: 768px) {
    .herosection {
        margin-top: -140px;
    }
  }
